.main-dashboard-component {
  margin-bottom: 60px;

  h1 {
    color: var(--Text, #211b3a);
    font-family: Urbanist;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 28px;
    /* 116.667% */
  }
}

.cards-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 16px;

  .stats-card {
    border-radius: 10px;
    background: #fff;
    padding: 16px;

    .card-heading {
      display: flex;
      align-items: center;
      gap: 8px;

      h3 {
        color: var(--Text, #211b3a);
        font-family: Urbanist;
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: 24px;
        letter-spacing: 0.15px;
        margin: 0px;
      }
    }
  }
}

.tableheading {
  h5 {
    color: #211b3a;
    font-family: Urbanist;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 28px;
    /* 140% */
  }

  input.form-control {
    border-radius: 8px;
    padding: 8px 12px;
    overflow: hidden;
    color: #868686;
    font-family: Urbanist;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.25px;
  }
}

.table th {
  font-family: Urbanist;
  color: #492fb2;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
  /* 114.286% */
  letter-spacing: 0.07px;
}

.table td {
  color: #333;
  font-family: Urbanist;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 150%;
  /* 21px */
  letter-spacing: 0.25px;
}

.custom-table {
  border: 1px solid #e5e7eb;
  border-radius: 8px;

  thead th {
    background-color: #eee;
    padding: 12px;
  }
}

.badge {
  border-radius: 8px;
  font-family: "Noto Sans";
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: var(--Line-Heights-10, 20px);
  /* 166.667% */
  letter-spacing: var(--Letter-Spacing-4, 0.25px);
  padding: 6px 12px;
}

.Approved {
  color: #3C9718;
  background: #EDF7E6;
}

.Unapproved {
  color: #B77224;
  background: #FEF1E7;
}

.dropdown-menu {
  border-radius: 8px;
  border: 1px solid #c6c6c6;
  background: #fff;
  cursor: pointer;
  padding: 8px 0px;

  .dropdown-item {
    padding: 8px 16px;
    display: flex;
    align-items: center;
    gap: 8px;
    font-family: Urbanist;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
  }

  .blue {
    color: #492fb2;
  }

  .black {
    color: #211b3a;
  }

  .red {
    color: #b7131a;
  }
}

.btn-check:checked+.btn,
.btn.active,
.btn.show,
.btn:first-child:active,
:not(.btn-check)+.btn:active {
  border-color: transparent;
}



.offcanvas {
  width: 420px; // width of the drawer
}

.upload-box {
  border-style: dashed;
  cursor: pointer;
  transition: background 0.3s;
}

.upload-box:hover {
  background: #f8f9fa;
}

.form-label {
  color: var(--Text-Dark, #212121);
  font-family: Urbanist;
  font-size: var(--Font-Size-2, 14px);
  font-style: normal;
  font-weight: 500;
  line-height: var(--Line-Heights-10, 20px);
  /* 142.857% */
  letter-spacing: var(--Letter-Spacing-2, 0.1px);
}

.upload-wrapper {
  .upload-box {
    display: block;
    border: 2px dashed #AFAFAF;
    border-radius: 10px;
    padding: 40px;
    cursor: pointer;
    transition: all 0.3s ease;
    background-color: #f8f9fa;

    &:hover {
      background-color: #e9f3ff;
    }
  }

  .upload-content {
    display: flex;
    align-items: center;
    color: #6c757d;
    justify-content: space-between;
  }

  .preview-box {
    text-align: center;

    img {
      max-width: 200px;
      border-radius: 10px;
      margin-top: 10px;
    }
  }
}



.modern-pagination .page-link.active {
  background-color: #f5f0ff;
  /* light purple */
  border: 1px solid #7c3aed;
  /* purple border */
  color: #7c3aed;
  /* purple text */
  font-weight: 600;
}

.modern-pagination .page-link:hover {
  background-color: #f2f2f2;
  color: #000;
}

.modern-pagination .page-item.disabled .page-link {
  opacity: 0.5;
  pointer-events: none;
}



/* publications.component.scss */

.custom-date {
  border-radius: 10px;        /* Rounded corners */
  border: 1px solid #0d6efd;  /* Bootstrap primary color */
  padding: 10px;
  font-size: 16px;
  transition: all 0.3s ease-in-out;
}

.custom-date:focus {
  border-color: #0a58ca;
  box-shadow: 0 0 8px rgba(13, 110, 253, 0.5); /* glow effect */
}

.custom-date::-webkit-calendar-picker-indicator {
  background-color: #0d6efd; 
  padding: 4px;
  border-radius: 50%;
  cursor: pointer;
  filter: invert(1); /* makes icon white */
}

